<template>
	<div class="city">
		<cheader></cheader>
		<search :citylist = "cits"></search>
		<citylist ></citylist>

	</div>
</template>

<script>
	import axios from 'axios';
	import cheader from '../components/city/header/header.vue'
	import search from '../components/city/search/search.vue'
	import citylist from '../components/city/citylist/citylist.vue'
 	
 	
	export default{
		data() {
			return{
				citylist:{},
				cits:{}
			}
		},
		components:{
			cheader,
			search,
			citylist,
		},
		mounted(){
			axios.get('./city.json').then((res) =>{
				this.citylist = (res.data.data)
				this.cits = (res.data.data.cities)
			})
		}
	}
</script>

<style lang="scss">
	.city{
		
	}
</style>