<template>
	<div>
		<div class="search">
			<input type="text" v-model="keyword" class="search-input" placeholder="输入城市或拼音" @input="search($event)">
		</div>	
		<div class="content" v-show="keyword">
			<ul>
				<li v-show="keyword" @click="hotclick(item.name)" class="border-bottom" v-for="item in list">
					{{item.name}}
				</li>
				<li v-show="nolist" class="border-bottom">没有匹配到数据</li>
			</ul>
		</div>
	</div>
	
</template>

<script>

	import { mapMutations } from 'vuex';

	export default{
		props:{
			citylist:{
				type: Object
			}
		},
		data (){
			return{
				keyword: '',
      			list: [],
      			timer: null
			}
		},
        watch :{
         keyword(){
            if(this.timer){
                clearTimeout(this.timer)
            }
            if(!this.keyword){
                this.list=[]
                return
            }
            this.timer=setTimeout(() => {
                const result=[]
                for (let i in this.citylist){
                    console.log(i)
                   this.citylist[i].forEach((value) => {
                       if(value.spell.indexOf(this.keyword)>-1||value.name.indexOf(this.keyword)>-1){
                           result.push(value)
                       }
                   });
                }
                //console.log(result)
                this.list=result
            }, 100);
 
         }
       },
       computed: {
       	 nolist (){
       	 	return !this.list.length
       	 },
       	 
       },
		methods:{
			search(event){
				this.content = true
				if (document.getElementsByTagName('input')[0].value == '') {
					this.content = false
					return
				}						
			},
			hotclick(hotcity){
		    	this.changecity(hotcity)
		    	this.$router.push('/')
		    },
		  ...mapMutations(['changecity'])
		}
	}
</script>

<style lang="scss">
	.search{
		height: .72rem;
		padding: 0 .1rem;
		background:#00bcd4;
		.search-input{
			width: 100%;
			text-align: center;
			border-radius: .06rem;
			height: .62rem;
			padding:0 .1rem;
			box-sizing: border-box;
			line-height: .62rem;
			color: #666;
		}
	}
	.content{
		position: absolute;
		top: 1.58rem;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 2;
		background:white;
		overflow: hidden;
		ul{
			height: 100%;
			overflow: auto;
			li{
				line-height: .62rem;
			    padding-left: .2rem;
			    background: #fff;
			    color: #666;
			}
		}
	}
</style>