<template>
	<div class="citylist" ref="ratings">
		<div class="area">
			<div class="title border-topbottom">当前城市</div>
			<div class="buttonList">
				<div class="buttonWrap">
					<div class="button">{{city}}</div>
				</div>
			</div>
		</div>
		<div class="area">
			<div class="title border-topbottom">热门城市</div>
			<div class="buttonList">
				<div class="buttonWrap" v-for="item in citylist.hotCities">
					<div class="button" @click="hotclick(item.name)">{{item.name}}</div>
				</div>
			</div>
		</div>
		<div class="area a" ref="foodsWrapper">
			<ul>
				<li class="liuk" v-for="(item,index) in citylist.cities">
					<div class="title border-topbottom">{{index}}</div>
					<ul class="itemlist">
						<li class="item border-bottom" @click="hotclick(inneritem.name)" v-for="inneritem in item">
							<div>{{inneritem.name}}</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<div class="alphabet" ref="menuWrapper">
			<ul>
				<li v-for="(item, index) in citylist.cities" :class="{'current':currentIndex === index}"  @click="selectMenu(index)" >{{index}}</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import BScroll from 'better-scroll';
	import { mapState, mapMutations} from 'vuex';
	export default{
		/*props:{
			citylist:{
				type: Object
			}
		},*/
		mounted(){
	      axios.get('./city.json').then((res) =>{
	        this.citylist = (res.data.data)
	        this.$nextTick(() => {
	        	this._initScroll();
	        	this._getHeight();
	        });
	      })
	    },
  		data (){
  			return{
  				listHeight: [],
			    scrollY: 0,
			    citylist:{}
  			}
  		},
  		methods:{
    		_initScroll () {
		      //better-scroll的实现原理是监听了touchStart,touchend事件，所以阻止了默认的事件（preventDefault）
		      //所以在这里做点击的话，需要在初始化的时候传递属性click,派发一个点击事件
		      //在pc网页浏览模式下，点击事件是不会阻止的，所以可能会出现2次事件，所以为了避免2次，可以在绑定事件的时候把$event传递过去
		      this.lefts = new BScroll(this.$refs.menuWrapper, {
		        click: true
		      })
		      this.rights = new BScroll(this.$refs.foodsWrapper, {
		        click: true,
		        probeType: 3 //探针的效果，实时获取滚动高度

		      })
		      //rights这个对象监听事件，实时获取位置pos.y
		      this.rights.on('scroll', (pos) => {
		        this.scrollY = Math.abs(Math.round(pos.y))
		      })
		    },
		    _getHeight () {
		      let rightItems = this.$refs.foodsWrapper.getElementsByClassName('liuk');
		      let height = 0
		      this.listHeight.push(height)
		      for(let i = 0; i < rightItems.length; i++){
		        let item = rightItems[i]
		        height += item.clientHeight
		        this.listHeight.push(height)
		      }
		      console.log(rightItems.length)
		      console.log(this.listHeight.length)
		    },
		    selectMenu(index){
		      if(!event._constructed){
		        return
		      }else{
		        let rightItems = this.$refs.foodsWrapper.getElementsByClassName('liuk');
		        let el = rightItems[index]
		        this.rights.scrollToElement(el, 300)
		        console.log(index)
		      }
		    },
		    hotclick(hotcity){
		    	this.changecity(hotcity)
		    	this.$router.push('/')
		    },
		    ...mapMutations(['changecity'])
    	},
    	computed:{
    		currentIndex () {
		      for(let i = 0; i < this.listHeight.length; i ++){
		        let height = this.listHeight[i]
		        let height2 = this.listHeight[i + 1]
		        //当height2不存在的时候，或者落在height和height2之间的时候，直接返回当前索引
		        //>=height，是因为一开始this.scrollY=0,height=0
		        if(!height2 || (this.scrollY >= height && this.scrollY < height2)){
		          return i
		        }
		        if(this.listHeight[this.listHeight.length - 1] - this.$refs.foodsWrapper.clientHeight <= this.scrollY){
		          if(this.clickTrue){
		            return this.currentNum
		          }else{
		            return (this.listHeight.length - 1)
		          }
		        }
		      }
		      //如果this.listHeight没有的话，就返回0
		      return 0
		    },
		    ...mapState({
				city: 'city'
			})
    	},
	}
</script>

<style lang="scss">
	.citylist{
		position: absolute;
		top: 1.58rem;
		left: 0;
		right: 0;
		bottom:0;
		overflow: hidden;
		.title{
			line-height: .54rem;
			background: #eee;
			padding-left: .2rem;
			color: #666;
			font-size: .26rem;
		}
		.border-topbottom{
			&:before{
				border-color: #ccc;
			}
			&:after{
				border-color: #ccc;
			}
		}
		.buttonList{
			overflow: hidden;
			padding:.1rem .6rem .1rem .1rem;
			.buttonWrap{
				width: 33.33%;
				float: left;
				.button{
					text-align: center;
					margin: .1rem;
					padding:.1rem 0;
					border:.02rem solid #ccc;
					border-radius:.06rem;
				}
			}
		}
		.itemlist{
			.item{
				line-height: .76rem;
				color: #666;
				padding-left:.2rem;

			}
			.border-bottom{
				&:before{
					border-color: #ccc;
				}
			}
		}
		.alphabet{
			display: flex;
			flex-direction: column;
			justify-content: center;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			width: .4rem;
			ul{
				li{
					text-align: center;
					line-height: .4rem;
					color:#00bcd4;
					&.current{
						color: red;
					}
				}
			}
		}
	}


	.a{
		height: 100%;
		overflow: hidden;
		
	}

</style>